import React, { useState, useEffect, useCallback } from "react";
import Trip from "./components/Trip";
import Loading from "./components/Splash";

import "mapbox-gl/dist/mapbox-gl.css";
import "./css/app.css";

const App = () => {
  const [data, setData] = useState({});
  const [loaded, setLoaded] = useState(false);

  const requestData = useCallback(async (type) => {
    const res = await fetch(`data/${type}.json`, {
      headers: {
        "Content-Type": "applicaion/json",
        Accept: "application/json",
      },
    });

    const data = await res.json();
    setData((prev) => ({ ...prev, [type]: data }));
  }, []);

  const totalRequestData = useCallback(async () => {
    await requestData("passenger");
    await requestData("trip");
    await requestData("wait_vehs");
    setLoaded(true);
  }, [])

  useEffect(() => {
    totalRequestData();
  }, []);

  return (
    <div className="container">
      {!loaded && <Loading />}
      {loaded && <Trip data={data}/>}
    </div>
  );
};

export default App;
