import React, { useState, useEffect, useCallback } from "react";

import DeckGL from "@deck.gl/react";
import { AmbientLight, PointLight, LightingEffect } from "@deck.gl/core";
import { ScatterplotLayer } from "@deck.gl/layers";
import { TripsLayer } from "@deck.gl/geo-layers";
import { Map } from "react-map-gl";

import Slider from "@mui/material/Slider";
import legend from "../img/legend.png";
import "../css/trip.css";

const ambientLight = new AmbientLight({
  color: [255, 255, 255],
  intensity: 1.0,
});

const pointLight = new PointLight({
  color: [255, 255, 255],
  intensity: 2.0,
  position: [-74.05, 40.7, 8000],
});

const lightingEffect = new LightingEffect({ ambientLight, pointLight });

const DEFAULT_THEME = {
  buildingColor: [74, 80, 87],
  trailColor0: [253, 128, 93],
  trailColor1: [23, 184, 190],
  effects: [lightingEffect],
};

const INITIAL_VIEW_STATE = {
  longitude: 127.4,
  latitude: 36.35,
  zoom: 11,
  minZoom: 3,
  maxZoom: 100,
  pitch: 0,
  bearing: 0,
};

const ICON_MAPPING = {
  marker: { x: 0, y: 0, width: 128, height: 128, mask: true },
};

const minTime = 0;
const maxTime = 1440;
const animationSpeed = 2;
const mapStyle = "mapbox://styles/spear5306/ckzcz5m8w002814o2coz02sjc";
const MAPBOX_TOKEN = `pk.eyJ1Ijoic3BlYXI1MzA2IiwiYSI6ImNremN5Z2FrOTI0ZGgycm45Mzh3dDV6OWQifQ.kXGWHPRjnVAEHgVgLzXn2g`; // eslint-disable-line

const returnAnimationTime = (time) => {
  if (time > maxTime) {
    return minTime;
  } else {
    return time + 0.01 * animationSpeed;
  }
};

const addZeroFill = (value) => {
  const valueString = value.toString();
  return valueString.length < 2 ? "0" + valueString : valueString;
};

const returnAnimationDisplayTime = (time) => {
  const hour = addZeroFill(parseInt((Math.round(time) / 60) % 24));
  const minute = addZeroFill(Math.round(time) % 60);
  return [hour, minute];
};

const dataFilter = (value, time) => {
  return value.filter((d) => d.duration[0] <= time && d.duration[1] >= time);
};

const ColorMap = (state) => {
  console.log(state);
  if (state === "assigned") {
    return [23, 184, 190];
  } else if (state === "in-service") {
    return [253, 128, 93];
  }
};

const Trip = ({ data }) => {
  const [time, setTime] = useState(minTime);
  const [animation] = useState([]);

  const filteredPassenger = dataFilter(data.passenger, time);
  const idleVehicle = dataFilter(data.wait_vehs, time);

  const animate = useCallback(() => {
    setTime((time) => returnAnimationTime(time));
    animation.id = window.requestAnimationFrame(animate);
  }, [animation]);

  useEffect(() => {
    animation.id = window.requestAnimationFrame(animate);
    return () => window.cancelAnimationFrame(animation.id);
  }, [animation, animate]);

  const layers = [
    new TripsLayer({
      id: "vehicle",
      data: data.trip,
      getPath: (d) => d.route,
      getTimestamps: (d) => d.timestamp,
      getColor: (d) => ColorMap(d.state),
      opacity: 1,
      widthMinPixels: 5,
      trailLength: 0.3,
      currentTime: time,
      shadowEnabled: false,
      pickable: true,
      onClick: (d) => {console.log(d.object)}
    }),
    new ScatterplotLayer({
      id: "passenger",
      data: filteredPassenger,
      radiusScale: 2,
      radiusMinPixels: 2,
      getPosition: (d) => d.location,
      getFillColor: (d) => [0, 255, 0],
      getRadius: 1,
    }),
    new ScatterplotLayer({
      id: "idle-car",
      data: idleVehicle,
      radiusScale: 2,
      radiusMinPixels: 2,
      getPosition: (d) => d.location,
      getFillColor: (d) => [255, 255, 0],
      getRadius: 1,
    }),
  ];

  const SliderChange = (value) => {
    const time = value.target.value;
    setTime(time);
  };

  const [hour, minute] = returnAnimationDisplayTime(time);

  return (
    <div className="trip-container" style={{ position: "relative" }}>
      <DeckGL
        effects={DEFAULT_THEME.effects}
        initialViewState={INITIAL_VIEW_STATE}
        controller={true}
        layers={layers}
      >
        <Map mapStyle={mapStyle} mapboxAccessToken={MAPBOX_TOKEN} />
      </DeckGL>
      <h1 className="time">TIME : {`${hour} : ${minute}`}</h1>
      <Slider
        id="slider"
        value={time}
        min={minTime}
        max={maxTime}
        onChange={SliderChange}
        track="inverted"
      />
      <img className="legend" src={legend} alt="legend"></img>
    </div>
  );
};

export default Trip;
